/**
 * This file defines the default font sizes to be used on headings and texts. The defaults come
 * from the palette defined in _variables.scss.
 */
/*=============================================
=            Body font family                  =
=============================================*/
/*=============================================
=            Body font sizes                  =
=============================================*/
/*=============================================
=            Heading font sizes               =
=============================================*/
/*=============================================
=            Head font weights                =
=============================================*/
.body {
  font-size: 12px;
}
@media (min-width: 48rem) {
  .body {
    font-size: 14px;
  }
}
@media (min-width: 64rem) {
  .body {
    font-size: 16px;
  }
}
@media (min-width: 80rem) {
  .body {
    font-size: 18px;
  }
}

.dse-text {
  margin: 0;
}

.dse-text-xs {
  font-size: 0.75rem;
}

.dse-text-sm {
  font-size: 0.875rem;
}

.dse-text-base {
  font-size: 1rem;
}

.dse-text-lg {
  font-size: 1.125rem;
}

.dse-text-xl {
  font-size: 1.5rem;
}