.body {
  font-size: 12px;
}
@media (min-width: 48rem) {
  .body {
    font-size: 14px;
  }
}
@media (min-width: 64rem) {
  .body {
    font-size: 16px;
  }
}
@media (min-width: 80rem) {
  .body {
    font-size: 18px;
  }
}

.dse-select {
  display: flex;
  flex-direction: column;
  position: relative;
}
.dse-select__label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  padding: 0.75rem;
  border: 1px solid var(--dse-form-border-color, #f1f2f3);
  background: var(--dse-form-bg-color, #fff);
  font-family: var(--dse-body-font-family, "Playfair", serif);
  cursor: pointer;
}
.dse-select__overlay {
  padding: 0rem;
  margin: 0rem;
  border: 1px solid var(--dse-form-border-color, #f1f2f3);
  background-color: var(--dse-form-bg-color, #fff);
  z-index: 99;
  position: absolute;
  width: 100%;
}